import Intro from 'app/js/courseset-manage/intro';

let listLength = $('#courses-list-table').find('tbody tr').length;
if(listLength == 2 ) {
  setTimeout(function() {
    $('.js-sidenav-course-menu').slideUp(function () {
        setTimeout(function() {
          let intro = new Intro(); 
          intro.initCourseListIntro('.js-sidenav');
        }, 500);
    });
  }, 500);
}